* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.05), #edf1f4);
    border-radius: 40px;
    box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.1), -15px -15px 20px white;
}

.range {
    width: 400px;
    height: 15px;
    appearance: none;
    background: #edf1f4;
    outline: none;
    border-radius: 15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1),
    -5px -5px 10px white,
    inset 5px 5px 5px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}
.range::-webkit-slider-thumb{
    appearance: none;
    width: 15px;
    height: 15px;background: white;
    border-radius: 50%;
    border: 2px solid #9f729d;
    box-shadow: -407px 0 0 400px #9f729d;
    cursor: pointer;
}
#rangeValue{
    position: relative;
    text-align: center;
    width: 60px;
    font-size: 1.25em;
    color: white;
    background: #9f729d;
    margin-left: 15px;
    border-radius: 25px;
    font-weight: 500;
    box-shadow: 5px 5px 10px rgba(0,0,0,0.1),
    -5px -5px 10px white,
    inset 5px 5px 10px rgba(0,0,0,0.1),
    inset -5px -5px 10px rgba(255,255,255,0.25);
}
